import "./styles.css";
const onclickAdd = () => {
  // å–å¾—ã¨åˆæœŸåŒ–
  const inputtext = document.getElementById("add-text").value; //å–å¾—
  document.getElementById("add-text").value = ""; //åˆæœŸåŒ–
  // DOMã‚’ä½œæˆã—ã¦å·®ã—è¾¼ã‚“ã§ã
  //divã®ä½œæˆ
  const div = document.createElement("div");
  div.className = "list-row";
  //li ã®ä½œæˆ
  const li = document.createElement("li");
  li.innerText = inputtext;
  //button(å®Œäº†)  ã®ä½œæˆ
  const compbutton = document.createElement("button");
  compbutton.innerText = "å®Œäº†";
  compbutton.addEventListener("click", () => {
    alert();
  });

  //button(å‰Šé™¤)  ã®ä½œæˆ
  const delbutton = document.createElement("button");
  delbutton.innerText = "å‰Šé™¤";
  delbutton.addEventListener("click", () => {
    //è¦ªã®divã‚’å‰Šé™¤ã™ã‚‹
    const deltaeget = delbutton.parentNode; //å–å¾—
    document.getElementById("inconp-list").removeChild(deltaeget); //è¦ªãŒå…¥ã£ã¦ã‚‹listã‹ã‚‰remove
  });

  //divã®å­è¦ç´ ã«li,button
  div.appendChild(li);
  div.appendChild(compbutton);
  div.appendChild(delbutton);
  console.log(div);

  //inconp-listã«divè¿½åŠ 
  document.getElementById("inconp-list").appendChild(div);
};
